@import "default-variables";

/**
 * Place your custom styles here.
 */

.path-catalog {

  .main-container {
    background-color: #f5f5f5;
    padding-top: 50px;

    .catalog-container {

      .region-content {
        display: flex;

        .main-header {
          display: none;
        }

        .view-catalog .view-content {
          display: flex;
          flex-wrap: wrap;
          justify-content: center;

          .views-row {
            // border: 3px solid red;
            flex: 300px 0 1;
            flex-wrap: wrap;
            margin-right: 20px;
            margin-bottom: 20px;

            background-image: url('../images/catalog_product_bg.png');
            background-size: cover;

            filter: drop-shadow(0 0 3px rgba(0, 0, 0, 0.3));
            // margin: 2px;

            .field-content > a {
              display: flex;
              flex-direction: column;
              align-items: center;
              padding: 10px;
              padding-bottom: 0;
              height: 350px;
              justify-content: space-between;

              &:hover {
                text-decoration: none;
              }

              .product-image {
                flex-grow: 1;

                img {
                  // margin: auto;
                }
              }

              .product-title {
                border-top: 1px solid black;
                border-bottom: 1px solid black;
                padding: 2px 0;
                text-transform: uppercase;
                color: black;
                height: 3.8em;
                line-height: 1.2em;
                width: 100%;
              }

              .product-colors {
                align-self: flex-start;

                ul {
                  display: flex;
                  margin-top: 10px;
                  margin-bottom: 0px;
                  padding: 0;
                  flex-wrap: wrap;

                  li {
                    list-style: none;
                    border: 2px solid black;
                    padding: 2px;
                    border-radius: 3px;
                    margin-right: 10px;
                    margin-bottom: 10px;

                    .color span {
                      display: none;
                    }

                    //.field--name-name {
                    //  display: none;
                    //}
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
